		methods:{
			geteshianyycfList() {
				let params = {
					page: 1,
					limit: 20,
				}
				this.$http({
					url: "eshianyycf/page",
					method: "get",
					params: params
				}).then(({ data }) => {
					if (data && data.code === 0) {
						this.eshianyycfList = data.data.list;
						let div = document.getElementById('eshianyycfTable')
						var scrollTop = -1
						function move() {
							if (div){
								if(div.scrollTop==scrollTop){
									div.scrollTo({
										top: 0, 
										behavior: "smooth"
									});
									return false
								}
								scrollTop = div.scrollTop
								div.scrollTo({ 
									top: div.scrollTop + 40, 
									behavior: "smooth"
								});
							}
								
						}
						this.eshianyycfTimer = setInterval(move, 2000);
						div.addEventListener('mouseenter', e => {
							e.stopPropagation()
							clearInterval(this.eshianyycfTimer);
						})
						div.addEventListener('mouseleave', e => {
							e.stopPropagation()
							this.eshianyycfTimer = setInterval(move, 2000);
						})
					}
				});
			},
			backClick(){
				this.$router.replace({ path: "/" });
			},
